<script>
    import { fly } from 'svelte/transition';      

	export let comment;

	let repliesVisible = false;
</script>

<div class="w-full">
	<div class="flex flex-row flex-none items-start">
		<button on:click={e => repliesVisible = !repliesVisible} class="max-w-xxs md:max-w-sm  lg:max-w-xl m-4 p-6 bg-white rounded-lg shadow hover:shadow-md" transition:fly="{{ x: -25, duration: 1000}}">
			<div class="font-sans text-base text-start leading-normal">
				<div class="comment">
					{@html comment.content}
				</div>
				<p class="text-gray-600">{comment.user} - {comment.time_ago} - {comment.comments.length} {comment.comments.length === 1 ? 'reply' : 'replies'}</p>
			</div>
		</button>
		{#if repliesVisible}
			<div class="flex flex-col">
				<div class="replies">
					{#each comment.comments as child}
						<svelte:self comment={child}/>
					{/each}
				</div>
			</div>
		{/if}
	</div>
</div>	