<script>
    import Comment from './Comment.svelte';

    export let item;
    export let offset;
    let commentsVisible = false;
    let commentItem;

	function comment_text() {
		const c = item.comments_count;
		return `${c} ${c === 1 ? 'comment' : 'comments'}`;
    }
    
    async function load_comments() {
        if (commentsVisible) {
            commentsVisible = false;
        } else {
            commentItem = await fetch(`https://node-hnapi.herokuapp.com/item/${item.id}`).then(r => r.json());
            commentsVisible = true;
        }
    }
</script>

<div class="flex flex-row items-start">
    <button on:click={load_comments}>
        <article class="max-w-xxs md:max-w-sm  lg:max-w-xl m-4 p-6 bg-white rounded-lg shadow hover:shadow-md">
            <h2 class="font-sans text-xl text-start text-gray-900 leading-tight"><a target="_blank" href={item.url}>{item.title}</a></h2>
            <p class="meta font-sans text-base text-start text-gray-600 leading-normal">{comment_text()} by {item.user} {item.time_ago}</p>
        </article>
    </button>

    {#if commentsVisible}
        <div>
            {#each commentItem.comments as comment}
                <Comment {comment}/>
            {/each}
        </div>
    {/if}
</div>