<script>
	import { onMount } from 'svelte';

	import Mainpage from './mainpage/Mainpage.svelte';
	import Hackernews from './hackernews/Hackernews.svelte';

	let mainpage = true;
	let hackernews = false;

	function changeSite() {
		if(mainpage) {
			mainpage = false;
			hackernews = true;
		} else {
			mainpage = true;
			hackernews = false;
		}
	}
</script>

<main>
	{#if mainpage}
		<Mainpage on:click={changeSite}/>
	{:else if hackernews}
		<Hackernews on:click={changeSite}/>
	{/if}
</main>